.header {
  font-family: Space Mono, serif;
  font-style: normal;
  background-image: url(../images/pictures/header-bi.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  &__content {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    font-family: Space Grotesk, serif;
    font-weight: 700;
    font-size: 36px;
    line-height: 36px;
    letter-spacing: 0px;

    @include onTablet {
      font-size: 48px;
      line-height: 48px;
    }

    @include onDesctop {
      font-size: 56px;
      line-height: 56px;
    }
  }

  &__bottom {
    padding-bottom: 40px;

    @include onTablet {
      padding-bottom: 65px;
    }

    @include onDesctop {
      padding-bottom: 92px;
    }
  }
}
